
import tabGroup from '@/components/group/tab'
import bigAnniu from '@/components/group/biganniu'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import {getCode} from '../../../api/user'

export default {
components: {
tabGroup: tabGroup,
bigAnniu: bigAnniu,
XInput,
Group,
Grid,
GridItem
},
data () {
return {
anniu: '下一步',
phone: '',
time: 60,
verCode: '',
showCountDownBtn: false,
countDownBtn: '60s',
getCodeBtn: '获取动态码'
}
},
props: {},
computed: {},
methods: {
setpassword () {
this.$router.push(
{
name: 'setpassword',
params: {userName: this.phone, verCode: this.verCode}
}
)
},
clear () {
this.phone = ''
},
getCode () {
getCode({userName: this.phone, codeType: 1}).then(res => {
console.log(res);
this.showCountDownBtn = true;
let timer = setInterval(() => {
this.time--;
if (this.time <= 0) {
this.time = 60;
this.showCountDownBtn = false;
this.getCodeBtn = '重新获取';
clearInterval(timer)
}
this.countDownBtn = this.time + 's';
}, 1000)
}).catch(e => {
console.log(e)
})
}
}
}



